<template>
    <div class="showContent checkInfoContent">
        <requestAndHisSubMenu :routekey="routekey"></requestAndHisSubMenu>
        <div class="box">
          <a-table
        :columns="columns"
        :dataSource="requestList"
        :pagination="pagination"
        :loading="loading"
        @change="handleTableChange"
      >
        <template slot="created_at" slot-scope="text">{{$util.timestampToDay(text)}}</template>
        <template slot="status" slot-scope="text">
          <span class="statusSpan">
            <span :class="text=='success'?'greenDot':'redDot'">
              <i class="iconfont amd-dot"></i>
            </span>
            {{text}}
          </span>
        </template>
        <template slot="operation" slot-scope="text,record,index">
            <span v-if="record.status=='success'" @click="gotoDetail(record)">></span>
        </template>
      </a-table>
        </div>
    </div>
    
</template>
<script>
import requestAndHisSubMenu from "@/components/requestAndHisSubMenu";
import { mapGetters } from "vuex";
import Cookies from "@/js/cookies";
import { mixinGetApiList } from "@/js/mixins";
export default {
  data() {
    const columns = [
      {
        title: "Company name",
        dataIndex: "request.companyName",
        align: "center",
        scopedSlots: { customRender: "id" }
      },
      {
        title: "Company address",
        dataIndex: "request.companyZipCode",
        align: "center",
        scopedSlots: { customRender: "name" }
      },
      {
        title: "Upload Time",
        dataIndex: "created_at",
        align: "center",
        scopedSlots: { customRender: "created_at" }
      },
      {
        title: "Status",
        dataIndex: "status",
        align: "center",
        scopedSlots: { customRender: "status" }
      },
      {
        title: "",
        key: "action",
        align: "center",
        width:'50px',
        scopedSlots: { customRender: "operation" }
      }
    ];
    return {
        routekey:["companyInfo","companyInfoHistory"],
        history:[],
        columns,
        historyKey: "app/v1/company-check/check",
        serviceApiId:'',
        requestList:[],
        pagination: { pageSize: 10 },
        loading:false,
    };
  },
  mixins:[mixinGetApiList],
  computed: {
    ...mapGetters(["apiHistoryList",'listPage'])
  },
  components: {
    requestAndHisSubMenu
  },
  methods:{
    ifGetData(){
      if(this.getApiListSuccess){
        console.log('获取到apilist了');
        let historyItem = this.apiHistoryList[this.historyKey];
        this.serviceApiId = historyItem._id;
        
        if(this.listPage){
            this.pagination.current=Number(this.listPage);
            this.getRequestlist(this.listPage);            
        }else{
            this.getRequestlist(1);
        }
      }else{
        console.log('meiyou');
      }
    },
    getRequestlist(currentPage) {
      let params = {
        serviceApiId: this.serviceApiId,
        page: currentPage,
        pageSize: this.pageSize,
        showFullLoading: currentPage == 1 ? true : false //只要加载第一页的时候全局loading
      };
      this.loading = true;
      this.$store.dispatch("getLog", params).then(
        res => {
          console.log(res);
           this.requestList=res.data.data;
          const pagination = { ...this.pagination };
          pagination.total = res.data.totalCount;
          this.loading = false;
          this.pagination = pagination;
        },
        error => {
          console.log(error);
        }
      );
    },
    handleTableChange(pagination) {
      this.pagination.current = pagination.current;
      this.getRequestlist(pagination.current);
    },
    gotoDetail(record){
      console.log(record);
      this.$store.dispatch("changePage", this.pagination.current);
      this.$router.push({name:'companyInfoHistoryDetail',params:{record}})
    }
  },
  created() {
    this.getApiList();
  },
  watch:{
    getApiListSuccess(oldval,val){
      console.log(oldval,val);
      this.ifGetData();
    }
  }
};
</script>




